@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.title{
    font-family: 'Pacifico', cursive !important;
    font-size:40px;
    color: whitesmoke;
    float: left;
    text-align: start;
    padding-left:20px ;
    width:60%;
    cursor: pointer;
}

.navbar{
    width:100%;
    background-color: lightseagreen;
    display: flex;
}

.post{
    float: right;
    width:5%;
    text-align: center;
    padding: 10px;
}

.profile{
    float: right;
    width:5%;
    padding: 10px;
}

.postParent{
    width: 100%;
    text-align: -webkit-center;
}
.createPost{
    width: 600px;
    text-align: center;
}

.profileHeader{
    width: 100%;
}

.thumbnail{
    width: 100%;
    display: flex;
}

.thumbnailDiv {
    width: 220px;
    height: 220px;
    line-height: 200px;
    border-radius: 50%;
    font-size: 20px;
    word-break:break-word;
    color: #fff;
    font-weight: bold;
    text-align: center;
   float: left;
}

.profileStats{
    width: 100%;
    float: right;
    text-align: center;
    display: flex;
}

.userName{
    font-weight: bold;
    cursor:pointer;
}

.profileName{
    textAlign: center;
    float: left;
    font-family: 'Pacifico', cursive !important;
    font-size:25px;
    color: black;
    float: left;
    text-align: start;
    padding: 3% 3% 3% 20px;
}

.subHeaders{
    font-size: 18px;
    padding: 20px;
}

.subHeadersValues{
    font-size: 18px;
    padding: 20px;
}
 tr > * {
    padding:10px
}


 td > * {
    padding:20px
}
